body {
  text-align: center;
  padding: 5vw 0 0 0;
}

body .gallery {
  font-family: Helvetica, Arial;
  position: relative;
  display: inline-block;
  vertical-align: top;
  background-color: #000;
  font-size: 0;
}

body .gallery:before {
  z-index: 0;
  content: '';
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -15px 0 0 -15px;
  border: 6px solid rgba(255, 255, 255, 0.5);
  border-top-color: transparent;
  border-bottom-color: transparent;
  border-radius: 50%;
  animation: rotate 1s linear infinite;
}

body .gallery .screen {
  position: relative;
  min-height: 350px;
  min-width: 600px;
}

body .gallery .screen img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  visibility: hidden;
  opacity: 0;
  transition: all 0.25s;
}

body .gallery .screen img.is-shown {
  position: relative;
  z-index: 0;
  visibility: visible;
  opacity: 1;
}

body .gallery .counter {
  visibility: hidden;
  position: absolute;
  top: auto;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 5px 0;
  z-index: 3;
  text-align: center;
  font-size: 0.75rem;
  color: #fff;
  text-shadow: #000 0 0 5px;
}

body .gallery .arrow {
  visibility: hidden;
  position: absolute;
  z-index: 2;
  opacity: 0.75;
  top: 50%;
  background-repeat: no-repeat;
  background-color: #fff;
  background-position: center;
  color: #000;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  margin-top: -16px;
  outline: none;
}

body .gallery .arrow:hover {
  opacity: 1;
  cursor: pointer;
}

body .gallery .arrow:active {
  opacity: 0.75;
}

body .gallery .arrow.arrow-prev {
  left: 10px;
  background-image: url(data:image/png;base64,);
}

body .gallery .arrow.arrow-next {
  right: 10px;
  background-image: url(data:image/png;base64,);
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(361deg);
  }
}
